import React from "react";
import { Row, Col, Card, Table, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import axios from "axios";
//import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css"; // Import
import Aux from "../../../hoc/_Aux";
import UpdateAgence from './Update.Agence';
import i18n from './../../../i18n';

var urlbackend = require("../../../env/env")();

class ListAgence extends React.Component {
  state = {
    Agences: [],
  };

  componentDidMount() {
    const config = {
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Headers": "x-access-token",
        "x-access-token": localStorage.getItem('token'),
        'Authorization': 'Bearer '+localStorage.getItem('token'),
      },
    };

    axios
      .get(urlbackend + "/agence", config)
      .then((response) => {
        console.log("this respon list", response.data);
        this.setState({ Agences: response.data.data });
      })
      .catch((e) => {
        console.log(e);
      });
  }

  Delette(Agence) {
    console.log("hellodelet", Agence.id);

   
  }
  render() {
    return (
      <Aux>
        <Row>
          <Col>
            <Card>
              <Card.Header>
                <Card.Title as="h5">
                <Link to="/Add/agency">
                    {" "}
                    <Button>{i18n.t('add-agency')}</Button>{" "}
                  </Link>{" "}
                  
                </Card.Title>
              </Card.Header>
              <Card.Body>
                <Table striped responsive>
                  <thead>
                    <tr>
                      <th>{i18n.t('agency')}</th>
                      <th>{i18n.t('phone-number')}</th>
                      <th>{i18n.t('address')}</th>
                      <th></th>
                      <th>{i18n.t('action-event')}</th>
                    </tr>
                  </thead>
                  <tbody>
                    {this.state.Agences.map((Agence, index) => (
                      <tr key={Agence.id}>
                        <td>{Agence.name} </td>
                        <td>{Agence.phone_number} </td>
                        <td>
                          {Agence.address} {Agence.city}{" "}
                        </td>
                        <td> {Agence.descriminator} </td>
                        <td>
                          {" "}
                          <Button
                            variant="danger"
                            onClick={() => this.Delette(Agence)}
                          >
                            {" "}
                            <span className="fa fa-trash"></span>
                          </Button>
                          <UpdateAgence agence={Agence} change={()=>this.componentDidMount()} />
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </Table>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Aux>
    );
  }
}

export default ListAgence;
