
import React, { useState,useEffect } from 'react';
import {
    Row,
    Col,
    Card,
    Button,
    Modal
} from "react-bootstrap";
import { Formik } from "formik";
import * as Yup from "yup";

import Aux from "../../../hoc/_Aux";
import CustomInput from "../../../Components/CustomInput";
import MessageModal from "./../../../Components/Alert/Alert";

import axios from 'axios';
import i18n from './../../../i18n';

var urlbackend = require("../../../env/env")();
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const SignupSchema = Yup.object().shape({
    // email: Yup.string().email("Invalid Email").required("Email is Required"),
    phone_number: Yup.string()
        .matches(phoneRegExp,i18n.t('error-phone-number'))
        .required(i18n.t('error-required-phone-number')),
    city: Yup.string()
        .min(2, i18n.t('error-short-governorate'))
        .max(20,i18n.t('error-long-governorate'))
        .required(i18n.t('error-required-governorate')),
    // country: Yup.string()
    //     .min(2, "Country is Too Short!")
    //     .max(20, "Country is Too Long!")
    //     .required("Country is Required"),
    name: Yup.string()
        .min(2,i18n.t('error-short-agency'))
        // .max(20,i18n.t('error-long-agency'))
        .required(i18n.t('error-required-agency')),
    address: Yup.string()
        .required(i18n.t('error-address')),
    // code: Yup.string()
    //     .min(2, "code is Too Short!")
    //     .max(20, "code is Too Long!")
    //     .required("code is Required"),
});
function UpdateAgence(props) {
    const [show, setShow] = useState(false);
    const [agencesUpdate] = useState(props.agence)
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [showMessageParams, setShowMessageParams] = useState({
        display:false,
        type:"success",
        message:null
    })
    const handlSubmit = (values) => {
        setShowMessageParams({
            display:false,
            type:"success",
            message:null
        });
        console.log(values);
        console.log(localStorage.getItem('token'));

        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };

        axios.put(urlbackend + "/agence/"+agencesUpdate.id, values, config)
            .then(response => {
                setShowMessageParams({
                    display:true,
                    type:"success",
                    message:i18n.t('success-update-agency')
                });
                console.log(response.data)
                props.change()
                handleClose()
            })
            .catch(e => {
                setShowMessageParams({
                    display:true,
                    type:"danger",
                    message:i18n.t('error-update-agency')
                });
                console.log(e)
            })
    };
    useEffect( () => {
        // console.log("show message value:",showMessage);
    }, [showMessageParams]);
    const showForm = ({
        values,
        errors,
        touched,
        handleChange,
        handleSubmit,
        handleBlur,
        setFieldValue,
        isSubmitting,
        dirty,
        isValid,
        handleReset,
    }) => {
        // console.log(errors);
        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5"></Card.Title>
                            </Card.Header>
                            {showMessageParams.display?
                            <MessageModal message={showMessageParams.message} design={showMessageParams.type} showit={showMessageParams.display} />
                            :null}
                            <Card.Body>
                                <Row>
                                    <Col md={6}>
                                        <CustomInput
                                            name="name"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.name}
                                            errors={errors.name}
                                            touched={touched.name}
                                            label={i18n.t('agency')}
                                        />


                                        <CustomInput
                                            name="phone_number"
                                            label={i18n.t('phone-number')}
                                            type="number"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.phone_number}
                                            errors={errors.phone_number}
                                            touched={touched.phone_number}
                                        />



                                    </Col>
                                    <Col md={6}>



                                        <CustomInput
                                            name="city"
                                            label={i18n.t('governorate')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.city}
                                            errors={errors.city}
                                            touched={touched.city}
                                        />


                                        <CustomInput
                                            name="address"
                                            label={i18n.t('address')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.address}
                                            errors={errors.address}
                                            touched={touched.address}
                                        />


                                    </Col>
                                </Row>

                                <Button
                                    disabled={Object.keys(errors).length>0}
                                    onClick={() => {
                                        handlSubmit(values);
                                        // handleReset();
                                        // console.log(values);
                                    }}
                                >
                                    {i18n.t('save')}
                    </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        );
    };

    return (
        <>
            <Button variant="info"
                onClick={()=>{
                    setShowMessageParams({
                        display:false,
                        type:"success",
                        message:null
                    });
                    handleShow();
                }}> <span className="fa fa-pencil"></span></Button>


            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>{i18n.t('update-agency')}</Modal.Title>
                </Modal.Header>
                <Modal.Body>


                    <Formik
                         initialValues={{
                            name: `${props.agence.name}`,
                            city: `${props.agence.city}`,
                            phone_number: `${props.agence.phone_number}`,
                            address: `${props.agence.address}`,
                          }}
                        onSubmit={(values, { setSubmitting }) => {
                            this.submitForm(values);
                            setSubmitting(false);
                        }}
                        validationSchema={SignupSchema}
                    >
                        {(props) => showForm(props)}
                    </Formik>

                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                    {i18n.t('close')}
            </Button>
                    {/* <Button variant="primary" onClick={handleClose}>
            Save Changes
            </Button> */}
                </Modal.Footer>
            </Modal>
        </>
    );
}

export default UpdateAgence